import { Navigate, useLocation, useParams } from "react-router-dom"
import { types, words } from "../data/words"
import { MenuItem, MenuList, ListItemText } from "@mui/material"

export const TypesCatalog = () => {
    let location = useLocation()
    let {
        word_type
    } = useParams()

    let currentType = types.find(type=>type.type == word_type)

    if(!currentType){
        return <Navigate to = '/catalog' replace />
    }

    return(
        <div>
            {words.map((word)=>{
                return <p>{word.word}</p>
            })}
        </div>
    )
}